<template>
  <div id="app">
    <h1>
      <a href="https://github.com/xuliangzhan/vue-element-extends">Editable</a>
    </h1>
    <div class="apimenu-header">
      <div class="apimenu-row">
        <router-link class="apimenu-link" :to="{name: 'EditableManual1'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableManual2'}" replace></router-link>
      </div>
      <div class="apimenu-row">
        <router-link class="apimenu-link" :to="{name: 'EditableClick1'}" replace>mini</router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick2'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick3'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick4'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick5'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick6'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick7'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick8'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick9'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick10'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableClick11'}" replace></router-link>
      </div>
      <div class="apimenu-row">
        <router-link class="apimenu-link" :to="{name: 'EditableDblclick1'}" replace>small</router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableDblclick2'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableDblclick3'}" replace></router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableDblclick4'}" replace></router-link>
      </div>
      <div class="apimenu-row">
        <router-link class="apimenu-link" :to="{name: 'EditableExcel1'}" replace>Excel</router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableExcel2'}" replace>Excel</router-link>
        <router-link class="apimenu-link" :to="{name: 'EditableExcel3'}" replace>Excel</router-link>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    activeName: {
      get () {
        return this.$route.name
      },
      set (value) {
        this.$router.replace({name: value})
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.apimenu-header {
  margin-bottom: 10px;
}
.apimenu-link {
  margin: 2px;
  padding: 4px 6px;
  color: #606266;
  border: 1px solid #dcdfe6;
  display: inline-block;
  font-size: 12px;
  border-radius: 2px;
  text-decoration: none;
}
.apimenu-link:hover {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
.apimenu-link.router-link-exact-active,
.apimenu-link.router-link-exact-active:hover {
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
}
</style>



