<template>
  <div v-loading="loading">
    <el-button type="success" size="mini" @click="$refs.editable.insert({flag3: false})"></el-button>
    <el-button type="success" size="mini" @click="$refs.editable.insertAt({flag3: false}, -1)"></el-button>
    <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
    <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
    <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
    <el-button type="warning" size="mini" @click="submitEvent"></el-button>
    <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
    <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
    <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
    <el-button type="primary" size="mini" @click="getAllEvent"></el-button>

    <p style="color: red;"></p>
    <p style="color: red;"></p>

    <el-editable ref="editable" stripe border show-summary :summary-method="getSummaries" :span-method="objectSpanMethod" size="medium" style="width: 100%">
      <el-editable-column type="index" width="55">
        <template slot="head">
          <i class="el-icon-setting" @click="dialogVisible = true"></i>
        </template>
      </el-editable-column>
      <template v-for="(item, index) in columnConfigs">
        <el-editable-column v-if="item.show" :key="index" v-bind="item"></el-editable-column>
      </template>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-popover placement="top" width="160" v-model="scope.row.flag3">
            <p></p>
            <div style="text-align: right; margin: 0">
              <el-button type="text" size="mini" @click="scope.row.flag3 = false"></el-button>
              <el-button type="primary" size="mini" @click="removeEvent(scope.row)"></el-button>
            </div>
            <el-button slot="reference" size="mini" type="danger"></el-button>
          </el-popover>
        </template>
      </el-editable-column>
    </el-editable>

    <el-dialog title="" :visible.sync="dialogVisible" width="300px" @open="openCustomEvent">
      <ul>
        <li v-for="(item, index) in columnConfigs" :key="index">
          <el-checkbox v-model="item.checked">{{ item.label }}</el-checkbox>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetCustomEvent"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveCustomEvent"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'
import columnsData from '@/common/json/editable/columns.json'

export default {
  data () {
    return {
      loading: false,
      dialogVisible: false,
      columnConfigs: [],
      sexList: [],
      regionList: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let sexPromise = this.getSexJSON()
      let regionPromise = this.getRegionJSON()
      this.findList()
      this.getColumnConfigs().then(data => {
        this.columnConfigs = data.map(column => {
          column.checked = true
          column.show = true
          switch (column.prop) {
            case 'sex':
              column.editRender.options = []
              sexPromise.then(rest => {
                column.editRender.options = rest
              })
              break
            case 'region':
              column.editRender.attrs = {options: []}
              regionPromise.then(rest => {
                column.editRender.attrs.options = rest
              })
              break
            case 'birthdate':
              column.editRender.attrs = {
                type: 'date',
                format: 'yyyy-MM-dd'
              }
              break
            case 'rate':
              column.editRender.type = 'visible'
              break
          }
          return column
        })
      })
    },
    findList () {
      this.loading = true
      this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    getSummaries (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = ''
          return
        }
        switch (column.property) {
          case 'sex':
            let rest = XEUtils.groupBy(data, column.property)
            sums[index] = `${rest[1] ? rest[1].length : 0}${rest[0] ? rest[0].length : 0}`
            break
          case 'age':
            sums[index] = `${XEUtils.mean(data, column.property)}`
            break
          case 'birthdate':
            sums[index] = `${XEUtils.toInteger(XEUtils.mean(data.map(item => XEUtils.toDateString(item[column.property], 'yyyy'))))}`
            break
          case 'rate':
            sums[index] = `${XEUtils.sum(data, column.property)}`
            break
          default:
            sums[index] = ''
            break
        }
      })
      return sums
    },
    objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex % 2 === 0) {
          return {
            rowspan: 2,
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    removeEvent (row) {
      row.flag3 = false
      this.$refs.editable.remove(row)
    },
    submitEvent () {
      let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
      this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
        this.findList()
      })
    },
    openCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.checked = column.show
      })
    },
    resetCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.checked = true
      })
    },
    saveCustomEvent () {
      this.dialogVisible = false
      this.columnConfigs.forEach(column => {
        column.show = column.checked
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 100)
      })
    },
    getColumnConfigs () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(columnsData, true)), 100)
      })
    },
    getDataJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(listData, true)), 350)
      })
    },
    getRegionJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>



